	border-color: #00b2ff
}

.pulse.pulse-info .pulse-ring {
	border-color: #7239ea
}

.pulse.pulse-warning .pulse-ring {
	border-color: #ffc700
}

.pulse.pulse-danger .pulse-ring {
	border-color: #f1416c
}

.pulse.pulse-dark .pulse-ring {
	border-color: #181c32
}

.pulse-ring {
	display: block;
	border-radius: 40px;
	height: 40px;
	width: 40px;
	position: absolute;
	animation: animation-pulse 3.5s ease-out;
	animation-iteration-count: infinite;
	opacity: 0;
	border-width: 3px;
	border-style: solid;
	border-color: #a1a5b7
}

@keyframes animation-pulse {
	0% {
		-webkit-transform: scale(.1, .1);
		opacity: 0
	}
	60% {
		-webkit-transform: scale(.1, .1);
		opacity: 0
	}
	65% {
		opacity: 1
	}
	100% {
		-webkit-transform: scale(1.2, 1.2);
		opacity: 0
	}
}

